html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: #0c0c0c;
  font-family: 'Roboto', sans-serif;
}

p {
  line-height: 1.35;
}

.container {
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
    "aside"
    "main"
  ;
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 500px) {
  .container {
    grid-template-columns: 30% 1fr;
    grid-template-areas:
      "aside main"
    ;
    width: 992px;
    /* padding: 0 15px; */
  }
}

@media (min-width: 992px) {
  .container {
    grid-template-columns: 30% 1fr;
    grid-template-areas:
      "aside main"
    ;
    width: 1170px;
  }
}

main {
  grid-area: main;
}

aside {
  grid-area: aside;
}

aside .inner,
main .inner {
  background: rgb(122, 122, 122);
  padding: 20px;
  border-radius: 25px;
}

ul {
  padding: 0;
  list-style: none;
}

h1 {
  font-size: 24px;
  margin-bottom: 0;
  font-weight: 700;
}

h2 {
  font-size: 20px;
  margin-top: 10px;
  color: #353535;
  ;
}

h3 {
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 0;
}

h4 {
  font-size: 15px;
  font-weight: 700;
}

section {
  margin-bottom: 4rem;
}

section h2 {
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(77, 77, 77);
  margin-bottom: 20px;
}

a {
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  -ms-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}


.fa {
  color: rgb(56, 56, 56);
  margin-right: .8rem;
  top: 4px;
}

.contact-links li {
  font-size: .9rem;
  margin-bottom: 15px;
}

.profileLinks li {
  display: inline-block;
}

.profileLinks li a {
  text-decoration: none;
  color: rgb(26, 30, 34);
}

.profileLinks li a:hover {
  color: rgb(64, 77, 77);
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
}

.skills-list li {
  font-size: 18px;
  margin: 0 3px 5px;
}

.workDates {
  color: #252225;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-center {
  text-align: center;
}

.center-block {
  display: block;
  margin: 0 auto;
}

.small-text {
  font-size: .8rem;
}

.img-circle {
  border-radius: 40%;
}

.divider {
  padding: 10px 20px 0;
  border-bottom: 1px solid #616161;
  margin-bottom: 20px;
}

.label {
  display: inline;
  padding: .2em .6em .3em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  color: #cac9c9;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: .25em;
}

.label-success {
  background-color: rgb(70, 68, 68);
}

.sr-only {
  position: absolute !important;
  /* Outside the DOM flow */
  height: 1px;
  width: 1px;
  /* Nearly collapsed */
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  /* IE 7+ only support clip without commas */
  clip: rect(1px, 1px, 1px, 1px);
  /* All other browsers */
}